import { initializeApp } from 'firebase/app';
import { getAuth } from "firebase/auth";
import {getDatabase, ref} from "firebase/database";
import { getStorage, uploadBytesResumable, getDownloadURL  } from "firebase/storage";
import {useState} from "react";


const firebaseConfig = {
    apiKey: "AIzaSyA_nkR60HJ_zgXS7iWlY5OviPpKtLuze1c",
    authDomain: "veynovkost-20987-ae8d1.firebaseapp.com",
    databaseURL: "https://veynovkost-20987-default-rtdb.europe-west1.firebasedatabase.app",
    projectId: "veynovkost-20987",
    storageBucket: "veynovkost-20987.appspot.com",
    messagingSenderId: "335565407827",
    appId: "1:335565407827:web:550f686371e9839fc768a6"
  };

const app = initializeApp(firebaseConfig);
 export const auth = getAuth(app);
 export const db = getDatabase(app);
 export const storage = getStorage(app);


 export const FileUploader = () => {
     const [file, setFile] = useState("");

     // progress
     const [percent, setPercent] = useState(0);

     // Handle file upload event and update state
     function handleChange(event) {
         setFile(event.target.files[0]);
     }

     const handleUpload = () => {
         if (!file) {
             alert("Please upload an image first!");
         }

         const storageRef = ref(storage, `/files/${file.name}`);

         // progress can be paused and resumed. It also exposes progress updates.
         // Receives the storage reference and the file to upload.
         const uploadTask = uploadBytesResumable(storageRef, file);

         uploadTask.on(
             "state_changed",
             (snapshot) => {
                 const percent = Math.round(
                     (snapshot.bytesTransferred / snapshot.totalBytes) * 100
                 );

                 // update progress
                 setPercent(percent);
             },
             (err) => console.log(err),
             () => {
                 // download url
                 getDownloadURL(uploadTask.snapshot.ref).then((url) => {
                     console.log(url);
                 });
             }
         );
     };

     return (
         <div>
             <input type="file" onChange={handleChange} accept="/image/*" />
             <button onClick={handleUpload}>Upload to Firebase</button>
             <p>{percent} "% done"</p>
         </div>
     );
 }

