import React, {useEffect, useState} from 'react';
import {onValue, ref} from "firebase/database";
import {db} from "./firebase";
import "./css.css";
import {SearchComponent} from "./SearchComponent";

export const CardsClientF = (props) => {

    const [cards, setCards] = useState([])
    const [filtered, setFiltered] = useState([])

    useEffect(() => {
        const dbRef = ref(db, 'executors');
        onValue(dbRef, (snapShot) => {
            let records = [];
            snapShot.forEach(childSnapshot => {
                let keyName = childSnapshot.key;
                let data = childSnapshot.val();
                records.push({"key": keyName, "data": data});
                // console.log({data});
            });
            setCards(records);
        });
    }, []);

    useEffect(() => {
        setFiltered(cards)
    }, [cards])

    console.log({filtered})

    
    return (
        <>
            <SearchComponent items={cards} setFilteredItems={setFiltered}/>
            <div style={{display: 'flex', flexWrap: 'wrap'}}>
                {filtered.map((card, index) => {
                    return (

                        <div key={card.key} className="card_container">
                            
                            <img
                                className="card_image"
                                src={card.data.image}
                                alt={""}
                            />
                            <div className='cardData'>
                            <ul>
                                <li >Адрес: {card.data.address}</li>
                                <li>Название: {card.data.name}</li>
                                <li>График: {card.data.workTime}</li>
                            <select>
                              <option>Показать номер</option>
                              <option>{card.data.phoneNumber}</option>
                           </select><br/>
                           <select> 
                              <option>Показать контакт</option>
                              <option>{card.data.telegram}</option>
                           </select>
                            </ul>
                           
                            </div>
                                        
                        </div>
                        
                
                    )
                })}
            </div>
        </>
    );
}